import React from "react";
import clsx from "clsx";
import Navbar from "./Navbar";
import {
  Icon,
  List,
  Drawer,
  AppBar,
  Toolbar,
  Divider,
  ListItem,
  useTheme,
  Typography,
  makeStyles,
  CssBaseline,
  ListItemText,
  ListItemIcon
} from "@material-ui/core";

const drawerWidth = 240;

const useStyles = makeStyles(theme => ({
  root: {
    display: "flex"
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen
    })
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen
    })
  },
  menuButton: {
    marginRight: 36
  },
  hide: {
    display: "none"
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: "nowrap"
  },
  drawerOpen: {
    width: drawerWidth,
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen
    })
  },
  drawerClose: {
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen
    }),
    overflowX: "hidden",
    width: theme.spacing(7) + 1,
    [theme.breakpoints.up("sm")]: {
      width: theme.spacing(9) + 1
    }
  },
  toolbar: {
    display: "flex",
    alignItems: "center",
    justifyContent: "flex-end",
    padding: theme.spacing(0, 1),
    ...theme.mixins.toolbar
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3)
  }
  //   paper: {
  //       padding: "1rem",
  //       minHeight: "80vh"
  //   }
}));

const options = [
  {
    view: "Home",
    label: "Inicio",
    icon: "home"
  },
  {
    view: "Proyectos",
    label: "Proyectos",
    icon: "trending_up"
  },
  {
    view: "Reportes",
    label: "Reportes",
    icon: "library_books"
  }
];

function Layout(props) {
  const classes = useStyles();
  const theme = useTheme();
  const [open, setOpen] = React.useState(false);

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar
        position="fixed"
        className={clsx(classes.appBar, {
          [classes.appBarShift]: open
        })}
      >
        <Toolbar>
          <Icon
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            edge="start"
            className={clsx(classes.menuButton, {
              [classes.hide]: open
            })}
          >
            menu
          </Icon>
          <Typography variant="h6" noWrap>
            SISTEMAS DE ENERGIA INTERNACIONAL
          </Typography>
        </Toolbar>
      </AppBar>
      <Drawer
        variant="permanent"
        className={clsx(classes.drawer, {
          [classes.drawerOpen]: open,
          [classes.drawerClose]: !open
        })}
        classes={{
          paper: clsx({
            [classes.drawerOpen]: open,
            [classes.drawerClose]: !open
          })
        }}
        open={open}
      >
        <div className={classes.toolbar}>
          <Icon onClick={handleDrawerClose}>
            {theme.direction === "rtl" ? "chevron_right" : "chevron_left"}
          </Icon>
        </div>
        <Divider />
        <List>
          {options.map(opt => (
            <ListItem key={opt.view} button>
              <ListItemIcon onClick={() => props.navigation.goMain(opt.view)}>
                <Icon fontSize="large">{opt.icon}</Icon>
              </ListItemIcon>
              <ListItemText primary={opt.label} />
            </ListItem>
          ))}
        </List>
      </Drawer>
      <main className={classes.content}>
        <div className={classes.toolbar} />
        <Navbar navigation={props.navigation} />
        <div>{props.children || <div />}</div>
      </main>
    </div>
  );
}

export default Layout;
