import React from "react";
import { Grid, Breadcrumbs, Button } from "@material-ui/core";

function Navbar({ navigation }) {
  return (
    <Grid item sm={12}>
      <Breadcrumbs>
        {navigation !== undefined &&
          navigation.routes.map((route, index) => (
            <Button
              onClick={() => navigation.goByIndex(index)}
              key={route.view}
            >
              {route.view}
            </Button>
          ))}
      </Breadcrumbs>
    </Grid>
  );
}

export default Navbar;
