import React from "react";
import "./App.css";
import Layout from "./components/Layout";
import PreLoader from "./components/Loader";
class App extends React.Component {
  state = {
    routes: [{ view: "Home", params: {} }]
  };

  host = window.location.href;

  componentDidMount() {
    if (sessionStorage.getItem("routes") !== null) {
      const routes = JSON.parse(sessionStorage.getItem("routes"));
      this.setState({ routes });
    }
  }

  render() {
    const navigation = {
      go: (view, params = {}) => {
        const { routes } = this.state;
        routes.push({ view, params });
        sessionStorage.setItem("routes", JSON.stringify(routes));
        this.setState({ routes });
      },
      goMain: (view, params = {}) => {
        sessionStorage.setItem("routes", JSON.stringify([{ view, params }]));
        this.setState({ routes: [{ view, params }] });
      },
      goBack: () => {
        const { routes } = this.state;
        routes.pop();
        sessionStorage.setItem("routes", JSON.stringify(routes));
        this.setState({ routes });
      },
      goByIndex: i => {
        const { routes } = this.state;
        const index = i + 1;
        const removeElements = routes.length - index;
        routes.splice(index, removeElements);
        sessionStorage.setItem("routes", JSON.stringify(routes));
        this.setState({ routes });
      },
      params: () => this.state.routes[index].params,
      routes: this.state.routes
    };

    const index = this.state.routes.length - 1;
    const View = React.lazy(() =>
      import("./views/" + this.state.routes[index].view)
    );

    return (
      <Layout navigation={navigation}>
        <React.Suspense fallback={<PreLoader show={true} />}>
          <View navigation={navigation} />
        </React.Suspense>
      </Layout>
    );
  }
}

export default App;
